<template>
  <div class="draggleChild" :style="{minWidth:width+'px',minHeight:height+'px'}">
    <vue-draggable-resizable :draggable="false" :resizable="true" :handles="['br']" @resizing="onResizing">
      <p>{{name}}</p>
      <img :src="image" />
      <div class="radar"></div>
    </vue-draggable-resizable>
    <!--<vue-draggable-resizable v-if="isToggle == 'cancel'" :draggable="false" :parent="true" :resizable="true" :handles="['br']" :drag-cancel="`.`+isToggle">
      <p :class="isToggle">{{name}}</p>
    </vue-draggable-resizable>
    <vue-draggable-resizable v-else :parent="true" :draggable="false" :resizable="true" :handles="['br']" :drag-handle="`.`+isToggle">
      <p :class="isToggle">{{name}}</p>
    </vue-draggable-resizable>-->
  </div>
</template>
<script>
import VueDraggableResizable from 'vue-draggable-resizable'
export default {
    props:{
      isToggle:{
        type:String,  //.drag  .cancel
        require:true
      },
      name:{
        type:String,
        require:true
      },
      image:{
        type:String,
        require:true
      },
      w:{
        type:Number,
        default:200  //传进来的宽
      },
      h:{
        type:Number,
        default:200  //传进来的高
      }
    },
    data () {
      return {
        width:200,
        height:200
      }
    },
    created(){
      this.width = this.w
      this.height = this.h
    },
    methods:{
      //实时改变高宽
      onResizing(a,b,c,d){
        this.width = c
        this.height = d
      }
    },
    computed: {
      
    },
    components: {
      VueDraggableResizable
    },
    created(){
      
    }
}
</script>

<style>
  .draggleChild{
    position:relative;
    /*min-width:200px;
    min-height:200px;*/
    margin:10px;
    float:left;
    cursor:move;
    overflow:hidden;
  }
  .draggleChild > div{
    display:flex;
    flex-direction:column;
  }
  .draggleChild > div > div{
    bottom:0!important;
    right:0!important;
  }
  .draggleChild img{
    flex:1;
    width:100%;
    height:100%;
  }
</style>
